import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

@Injectable()
export class TwitterService {
    
    constructor(private http: Http) { }

    getSentimento(pres) {
        const url = 'http://35.237.82.186/app1/candidato_sent_twitter?pres='+ pres +'&mode=polarized';
        return new Promise(resolve => {
            this.http.get(url).subscribe(data => {
                const obj = data.json();
                resolve(obj);
            });
        });
    }
    getNumMencoes(pres) {
        let obj;
        if (obj) {
            return Promise.resolve(obj);
        }
        const url = 'http://35.237.82.186/app1/candidato_mencoes?pres='+ pres +'&fonte=twitter';
        return new Promise(resolve => {
            this.http.get(url).subscribe(data => {
                obj = data.json();
                resolve(obj);
            });
        });
    }
}
