import { CommonModule } from '@angular/common';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { HttpModule } from '@angular/http';
import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { NoticiasService } from './shared/services/noticias/noticias';
import { TwitterService } from './shared/services/twitter/twitter';
import { PresidenteNoticias } from './layout/noticias/noticias.model';
import { CadastroService } from './shared/services/cadastro/cadastro';
import { LogarService } from './shared/services/login/logar';
import { AtivarComponents } from './shared/services/ativarComponents';
import { TwitterModel } from './layout/twitter/twitter.model';
import { GeralService } from './shared/services/geral/geral';

export const createTranslateLoader = (http: HttpClient) => {
    return new TranslateHttpLoader(http, './assets/i18n/', '.json');
};

@NgModule({
    imports: [
        HttpModule,
        CommonModule,
        BrowserModule,
        BrowserAnimationsModule,
        HttpClientModule,
        TranslateModule.forRoot({
            loader: {
                provide: TranslateLoader,
                useFactory: createTranslateLoader,
                deps: [HttpClient]
            }
        }),
        AppRoutingModule
    ],
    declarations: [AppComponent],
    providers: [NoticiasService, 
        TwitterService, 
        PresidenteNoticias, 
        CadastroService, 
        LogarService, 
        AtivarComponents,
        TwitterModel,
        GeralService],
    bootstrap: [AppComponent]
})
export class AppModule { }
