import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

@Injectable()
export class NoticiasService {

    constructor(private http: Http) { }

    getMencoes(pres) {
        let obj;
        if (obj) {
            return Promise.resolve(obj);
        }
        const apiUrl = 'http://35.237.82.186/app1/candidato_mencoes?pres=' + pres + '&fonte=noticias';
        return new Promise(resolve => {
            this.http.get(apiUrl).subscribe(data => {
                obj = data.json();
                resolve(obj);
            });
        });
    }

    getNoticias(pres) {
        let obj;
        if (obj) {
            return Promise.resolve(obj);
        }
        const apiUrl = 'http://35.237.82.186/app1/noticias_filtro?filtro='+ pres +'&cases=TRUE';
        return new Promise(resolve => {
            this.http.get(apiUrl).subscribe(data => {
                obj = data.json();
                resolve(obj);
            });
        });
    }
    getNoticiasPortal(pres, portal) {
        let obj;
        if (obj) {
            return Promise.resolve(obj);
        }
        const apiUrl = 'http://35.237.82.186/app1/noticias_filtro?filtro='+ pres +'&cases=TRUE&portall='+portal;
        return new Promise(resolve => {
            this.http.get(apiUrl).subscribe(data => {
                obj = data.json();
                resolve(obj);
            });
        });
    }
}
